:root {
  --wishlist-item-border-color: var(--expandable-content-divider-color); }

.MyAccountMyWishlist {
  --myaccount-wihslist-products-margin-bottom: 0; }
  .MyAccountMyWishlist-Products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: max-content;
    grid-column-gap: 2.4rem; }
    @media (min-width: 1370px) {
      .MyAccountMyWishlist-Products {
        grid-template-columns: repeat(4, 1fr); } }
    @media (min-width: 811px) and (max-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
      .MyAccountMyWishlist-Products {
        grid-template-columns: repeat(2, 1fr); } }
    @media (max-width: 810px) {
      .MyAccountMyWishlist-Products {
        grid-column-gap: 2.8rem;
        grid-template-columns: 1fr;
        border: 0;
        margin: 0 calc(-1 * var(--my-account-wrapper-padding-mobile)); } }
    @media (min-width: 811px) {
      .MyAccountMyWishlist-Products {
        grid-column: 2; } }
    @media (max-width: 810px) {
      .MyAccountMyWishlist-Products {
        margin-bottom: var(--myaccount-wihslist-products-margin-bottom); } }
  .MyAccountMyWishlist-ActionBar {
    display: flex;
    padding: 0 0 1.2rem;
    justify-content: flex-end; }
    @media (min-width: 811px) {
      .MyAccountMyWishlist-ActionBar {
        position: absolute;
        top: -55px;
        right: 0; } }
    @media (max-width: 810px) {
      .MyAccountMyWishlist-ActionBar {
        background-color: var(--my-account-content-background);
        bottom: var(--navigation-tabs-height);
        display: block;
        left: 0;
        padding: 1.4rem 1.4rem;
        position: fixed;
        width: 100%; } }
  .MyAccountMyWishlist-ActionBarActionWrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px; }
  .MyAccountMyWishlist-NoProductsButton {
    margin-top: 1.2rem; }
    @media (max-width: 810px) {
      .MyAccountMyWishlist-NoProductsButton {
        margin-top: 1.4rem; } }
  @media (max-width: 810px) {
    .MyAccountMyWishlist-Button {
      width: 100%; } }
  .MyAccountMyWishlist-ClearRemoveItemsButton {
    text-transform: uppercase;
    color: initial; }
    .MyAccountMyWishlist-ClearRemoveItemsButton.Button {
      width: auto; }
  .MyAccountMyWishlist-ClearWishlistButton {
    font-weight: bold;
    text-transform: uppercase; }
    @media (min-width: 811px) {
      .MyAccountMyWishlist-ClearWishlistButton {
        order: 1;
        margin-left: 15px; } }
    .MyAccountMyWishlist-ClearWishlistButton, .MyAccountMyWishlist-ClearWishlistButton:not([disabled]):focus, .MyAccountMyWishlist-ClearWishlistButton:not([disabled]):hover {
      color: #ed3f3f; }
    .MyAccountMyWishlist-ClearWishlistButton.Button {
      width: auto; }
  .MyAccountMyWishlist-ShareWishlistButton {
    background-image: linear-gradient(to right, var(--header-color) 15%, var(--header-background) 0);
    background-repeat: repeat-x;
    background-size: 12px calc(var(--header-icon-stroke-width) + 1px);
    border-radius: 1px;
    padding: calc(9px - calc(var(--header-icon-stroke-width) + 1px));
    border-bottom: calc(var(--header-icon-stroke-width) + 1px) solid var(--header-color);
    border-left: calc(var(--header-icon-stroke-width) + 1px) solid var(--header-color);
    border-right: calc(var(--header-icon-stroke-width) + 1px) solid var(--header-color);
    height: 0;
    margin: 1.4rem 2rem 0 0; }
    @media (min-width: 811px) {
      .MyAccountMyWishlist-ShareWishlistButton {
        background-image: linear-gradient(to right, var(--header-color) 20%, var(--my-account-content-background) 0);
        background-size: 15px calc(var(--header-icon-stroke-width) + 1px);
        padding: calc(11px - calc(var(--header-icon-stroke-width) + 1px)); } }
    .MyAccountMyWishlist-ShareWishlistButton::before, .MyAccountMyWishlist-ShareWishlistButton::after {
      content: '';
      position: absolute; }
    .MyAccountMyWishlist-ShareWishlistButton::before {
      background-color: var(--header-color);
      height: 10px;
      left: 6px;
      top: -4px;
      width: calc(var(--header-icon-stroke-width) + 1px); }
      @media (min-width: 811px) {
        .MyAccountMyWishlist-ShareWishlistButton::before {
          height: 13px;
          left: 8px;
          top: -6px;
          width: 2px; } }
    .MyAccountMyWishlist-ShareWishlistButton::after {
      height: 4px;
      left: 4px;
      top: -4px;
      transform: rotate(45deg);
      width: 4px;
      border-left: calc(var(--header-icon-stroke-width) + 1px) solid var(--header-color);
      border-top: calc(var(--header-icon-stroke-width) + 1px) solid var(--header-color); }
      @media (min-width: 811px) {
        .MyAccountMyWishlist-ShareWishlistButton::after {
          height: 6px;
          left: 5px;
          top: -7px;
          width: 6px; } }
    .MyAccountMyWishlist-ShareWishlistButton[disabled] {
      opacity: .25;
      cursor: not-allowed; }
